.switch {
  flex-shrink: 0;
  position: relative;
  display: block;
  width: 110px;
  height: 100%;
  border-radius: 18px;
  padding: 0;
  background: #fefdfd; }

.switch-input {
  display: none; }

.switch-label {
  position: relative;
  display: block;
  font-size: 12px;
  background: #fefdfd;
  border-radius: inherit;
  border: solid 2px #fefdfd;
  padding: 5px 10px; }
  .switch-label:hover {
    cursor: pointer; }

.switch-label:before {
  content: attr(data-off);
  position: absolute;
  right: 10px;
  color: #bcbcbc;
  text-align: right; }

.switch-label:after {
  content: attr(data-on);
  position: relative;
  left: 0;
  opacity: 0;
  color: #bcbcbc; }

.switch-input:checked ~ .switch-label:before {
  opacity: 0; }

.switch-input:checked ~ .switch-label:after {
  opacity: 1;
  color: #FFCA00; }

.switch-handle {
  position: absolute;
  top: 4px;
  left: 5px;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  background: #d5d5d5;
  box-shadow: none; }
  .switch-handle:hover {
    cursor: pointer; }

.switch-handle:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 12px;
  height: 12px;
  background: #fefdfd;
  border-radius: 50%;
  box-shadow: inset 0 1px rgba(0, 0, 0, 0.02); }

.switch-input:checked ~ .switch-handle {
  left: calc(100% - 5px);
  transform: translateX(-100%);
  background: #FFCA00; }

.switch-label, .switch-label:before, .switch-label:after, .switch-handle {
  transition: all 0.5s; }

